import React from "react";
import { BrowserRouter as Router, Switch, Route, Link,NavLink } from "react-router-dom";
import './Main.scss';
import Home from './../modules/blog/components/home/Home'
import Hot from './../modules/blog/components/hot/Hot'
import Sports from './../modules/blog/components/sports/Sports'

export default function Main() {
  return (
    <Router>
      <div>
        <nav className="navbar navbar-expand-lg navbar-light bg-light">
          <Link className="navbar-brand" to="/">
            AND News
          </Link>
          <button
            className="navbar-toggler"
            type="button"
            data-toggle="collapse"
            data-target="#navbarSupportedContent"
            aria-controls="navbarSupportedContent"
            aria-expanded="false"
            aria-label="Toggle navigation"
          >
            <span className="navbar-toggler-icon"></span>
          </button>

          <div className="collapse navbar-collapse" id="navbarSupportedContent">
            <ul className="navbar-nav mr-auto">
              <li className="nav-item" >
                <NavLink className="nav-link" to="/hot" activeClassName='is-active'>
                  Hot News
                </NavLink>
              </li>
              <li className="nav-item">
                <NavLink className="nav-link" to="/sports" activeClassName='is-active'>
                  Sports
                </NavLink>
              </li>
              <li className="nav-item dropdown">
                <a
                  className="nav-link dropdown-toggle"
                  href="#"
                  id="navbarDropdown"
                  role="button"
                  data-toggle="dropdown"
                  aria-haspopup="true"
                  aria-expanded="false"
                >
                  More
                </a>
                <div className="dropdown-menu" aria-labelledby="navbarDropdown">
                  <a className="dropdown-item" href="#">
                    Entertainment
                  </a>
                  <a className="dropdown-item" href="#">
                    Entertainment 1
                  </a>
                  <div className="dropdown-divider"></div>
                  <a className="dropdown-item" href="#">
                    Entertainment 2
                  </a>
                </div>
              </li>
            </ul>
            <form className="form-inline my-2 my-lg-0">
              <input
                className="form-control mr-sm-2"
                type="search"
                placeholder="Search"
                aria-label="Search"
              />
              <button
                className="btn btn-outline-success my-2 my-sm-0"
                type="submit"
              >
                Search
              </button>
            </form>
          </div>
        </nav>
        <div className="container">
          <div className="row">
            <div className="col-12">
              <Switch>
                <Route exact path="/">
                  <Home />
                </Route>
                <Route path="/hot">
                  <Hot />
                </Route>
                <Route path="/sports">
                  <Sports />
                </Route>
              </Switch>
            </div>
          </div>
        </div>
      </div>
    </Router>
  );
}

